@import url(https://fonts.googleapis.com/css2?family=Karla&Fira+Sans&family=Poppins&display=swap);
.container {
  font-family: 'Karla', sans-serif;
}
h2, h3, h4 {
  font-family: 'Karla', sans-serif;
}
.navbar {
  display: flex;
  flex-direction: row;
  align-items: center;
  width: 100%;
}
.logo {
  margin-left: 7vw;
}
.download {
  background-color: #ff6d00;
  color: whitesmoke;
  border: 0px;
  font-family: 'Poppins', sans-serif;
  border-radius: 10px;
  padding: 12px 30px 12px 30px;
  position: absolute;
  right: 7vw;
  text-decoration: none;
}
.intro {
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  padding: 10px;
}
.introtext {
  align-self: center;
}
.screenshot {
  width: 400px;
  height: 300px;
  border-radius: 10px;
}
.mdownload {
  border-color: #003566;
  background-color: #edf2fb;
  border-style: solid;
  border-width: 2px;
  font-family: 'Poppins', sans-serif;
  color: #003566;
  border-radius: 10px;
  padding: 12px 30px 12px 30px;
}
.sdownload {
  border-color: #003566;
  background-color: #ffffff;
  border-style: solid;
  border-width: 2px;
  font-family: 'Poppins', sans-serif;
  color: #003566;
  border-radius: 10px;
  padding: 10px;
}
.more {
  padding: 10px;
  display: flex;
  justify-content: space-evenly;
}
.stack {
  padding: 4.5px;
  border-radius: 5px;
}
.source {
  padding: 4.5px;
}
.stackimages {
  display: flex;
  justify-content: space-around;
}
.icon {
  width: 50px;
  height: auto;
}
.footer {
  background-color: #ffffff;
  padding: 10px;
  display: flex;
  justify-content: space-around;
  align-items: center;
}
.qlinks {
  display: flex;
  flex-direction: column;
  align-content: space-around;
}
.link {
  padding: 5px;
  text-decoration: none;
  color: #343a40;
  font-size: 15px;
}
.bottomline {
  font-size: 14px;
  text-align: center;
}

@media only screen and (max-width: 600px) {
  .intro {
    display: flex;
    flex-direction: column-reverse;
    padding: 10px;
  }
  .screenshot {
    width: 100%;
    height: auto;
    border-radius: 10px;
  }
  .more {
    padding: 10px;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
  }
  .icon {
    width: 40px;
    height: auto;
  }
}
